import Vue from 'vue'
import VueRouter from 'vue-router'

//components
//import PeopleManagement from '../views/peoplemanagement.vue'
import DDT from '../views/datac_test.vue';
import RelProd from '../views/releasedproduct.vue';
import ProductDetail from '../views/productdetail.vue';
//import DDTT from '../views/datacc_test.vue';
import MessageAudit from '../views/messageaudit.vue';
import RecommendedSetting from '../views/recommendedsetting.vue';


Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'releasedproduct',
        component: RelProd
    },
    {
        path: '/releasedproduct',
        name: 'peoplemanagement',
        component: RelProd
    },
    {
        path: '/datactest',
        name: 'datactest',
        component: DDT
    },
    {
        path: '/productdetail',
        name: 'productdetail',
        component: ProductDetail
    },
    {
        path: '/messageaudit',
        name: 'messageaudit',
        component: MessageAudit
    },
    {
        path: '/recommendedsetting',
        name: 'recommendedsetting',
        component: RecommendedSetting
    },


]

const router = new VueRouter({
    routes
})

export default router