  TooltipComponent,
  //LegendComponent
  GridComponent,
} from "echarts/components";
import VChart  from "vue-echarts";


use([
  CanvasRenderer,
  PieChart,
  //TitleComponent,
  TooltipComponent,
  //LegendComponent
  GaugeChart,
  BarChart,
  GridComponent,
]);

export default {
